import React from 'react';
import Navbar from '../components/navbar';
import "./../css/loadingPage.css"
import "./../css/generalStats.css"
import "bootstrap-icons/font/bootstrap-icons.css"
// import { server } from '../conf';
import GeneralStatCard from '../components/generalStatCard';

const queryParams = new URLSearchParams(window.location.search)
// queryParams.set("username", "Idiot_Dev")

class GeneralStats extends React.Component {
	constructor(props) {
		super(props);
		this.loadingText = "Loading..."
		this.state = {
			networkLevel: this.loadingText,
			validUsername: "Please Enter A Player Username",
			selectedUser: false
		}
	}
	componentDidMount() {
		this.loadData()
	}
	loadData() {
		this.setState({
			networkLevel: 10
		})
	}
	render() {
		if (!this.state.selectedUser) {
			return <div>
				<Navbar app="General Stats" />
				<div className="generalStatsPage page">
					<h1>{this.state.validUsername}</h1>
					<input type="text" onChange={(e) => {
						this.setState({
							validUsername: e.target.value
						})
					}} />
					<div className="submitBtn" onClick={(e) => {
						this.setUsername()
					}}>
						<p>Submit</p>
					</div>
				</div>
			</div>
		} else {
			return <div>
				<Navbar app="General Stats" />
				<div className="generalStatsPage page">
					<div>
						<i class="bi bi-arrow-clockwise reload" onClick={
							(e) => {
								this.setState({
									selectedUser: false,
									validUsername: "Please Enter A Player Username"
								})
							}
						}></i>
						<h1>General Stats For {queryParams.get(`username`)}</h1>
					</div>
					<div className="cards">
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
						<GeneralStatCard name="Network Level" value={this.state.networkLevel} />
					</div>
				</div>
			</div>;
		}
	}
	setUsername() {
		var e = this.state.validUsername
		if (e !== "" || e !== null || e !== undefined) {
			queryParams.set("username", e)
			this.setState({
				selectedUser: true
			})
		} else {
			this.setState({
				validUsername: "Please Enter A Valid Username."
			})
		}
	}
}

// queryParams.set("username", "")

export default GeneralStats;
